使用flex布局
*/
.soulwelcome {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.soulwelcomehide {
  opacity: 0;
  visibility: hidden;
}
.soulwelcome img {
  margin: 25%;
  width: 50%;
}







































































